import React, { Component } from 'react';
import { HashRouter as Router, Route } from "react-router-dom";
import loadable from '../../utils/lazyLoad';
import "./index.less";

const GoJSintro = loadable(() => import('./Intro'));
const GoJSfirst = loadable(() => import('./First'));
const GoJSsecond = loadable(() => import('./Second'));

export default class index extends Component {
    render() {
        return (
            <div>
                <Router>
                    <Route path="/gojs/1" render={() => (<GoJSfirst ref={r => this.child = r} />)} />
                    <Route path="/gojs/2" render={() => (<GoJSsecond />)} />
                    <Route exact path={["/gojs/intro", "/gojs"]} render={() => (<GoJSintro />)} />
                </Router>
            </div >
        )
    }
}
